.signUpForm {
  align-items: center;
  background-color: var(--neutral-white);
  border-radius: 8px;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 8px 4px 0px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding: 24px;
}

.signUpForm div {
  width: 100%;
}

.input {
  border: 1px solid var(--neutral-grayish-blue);
  border-radius: 4px;
  color: var(--neutral-dark-blue);
  padding: 16px 0;
  position: relative;
}

.input:focus-within {
  border-color: var(--accent-blue);
  border-width: 2px;
}

.input input {
  border: none;
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 0.02rem;
  padding: 0 16px;
  width: 100%;
}

.input input:focus-visible {
  outline: none;
}

.input i {
  position: absolute;
  right: 16px;
  top: 50%;
  transform: translateY(-50%);
}

.message {
  display: block;
  font-size: 11px;
  font-weight: 500;
  margin-top: 8px;
  text-align: right;
}

.action {
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 16px;
  width: 100%;
}

.action button {
  background: var(--primary-green);
  border: none;
  border-radius: 4px;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.25);
  color: var(--neutral-white);
  font-weight: 600;
  letter-spacing: 0.02em;
  padding: 16px 0;
  text-transform: uppercase;
  width: 100%;
}

.action button:disabled {
  cursor: not-allowed!important;
  filter: grayscale(1)!important;
}

.action button:hover {
  cursor: pointer;
  filter: brightness(110%);
}

.action p {
  color: var(--neutral-grayish-blue);
  font-size: 11px;
  line-height: 21px;
  max-width: 248px;
  text-align: center;
}

.action b {
  color: var(--primary-red);
}

@media (min-width: 1280px) {
  .signUpForm {
    padding: 40px;
  }

  .input input {
    padding: 0 32px;
  }

  .input i {
    right: 32px;
  }

  .action p {
    max-width: 100%;
  }
}

.error.input {
  border-color: var(--primary-red);
  border-width: 2px;
}

.error input, i, em {
  color: var(--primary-red);
}